.row-1 {
  background-image: url("../assets/images/image7.JPEG");
  height: 500px;
  background-position: center;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #eb8864;
  /* background: linear-gradient(#d1958086, #d1958086),
    url("../assets/images/image7.JPEG"); */
}

.heading {
  color: white;
  filter: drop-shadow(5px 5px 15px #000000);
}

.b-p-card {
  height: 190px;
}

.puppies-col {
  background-image: url("../assets/images/image14.JPEG");
  height: 500px;
  background-position: center;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #eb8864;
  padding: 0px;
  padding-left: 0.1px;
}
.our-dogs-col {
  background-image: url("../assets/images/image17.JPEG");
  height: 500px;
  background-position: center;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-color: #eb8864;
  padding: 0px;
  padding-right: 0.1px;
}

.overlay {
  width: 100%;
  height: 100%;
  background-color: #eab09bcc;
  transition: 0.9s;
}
.overlay:hover {
  cursor: pointer;
  transition-timing-function: ease-in-out;
  transition: 0.9s;
  background-color: #eed9d14e;
}

.av-img {
  height: 300px;
}

footer {
  height: 50px;
}

.link-hover {
  color: white;
}
.link-hover:hover {
  color: rgb(233, 233, 233);
}

@media only screen and (max-width: 1036px) {
  .b-p-card {
    height: 210px;
  }
}

@media only screen and (max-width: 992px) {
  .b-p-card {
    height: fit-content;
  }
}
